@use '../../../assets/styles/abstracts/colors' as colors;

.apps {
  padding: 0 0 130px;

  &__lead {
    padding: 20px 0 168px;

    &-intro-container {
      display: flex;
      flex-direction: row;
      gap: 142px;
    }

    &-title {
      max-width: 776px;
    }

    &-intro-text-container {
      min-height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      max-width: 420px;
    }

    &-list {
      display: flex;
      justify-content: space-between;
      list-style: none;
      margin: 0;
      padding: 150px 0 0;
    }

    &-list-item {
      width: 79px;
      height: 98px;
      margin: 0;
      padding: 0;
      background-position: center top;
      background-blend-mode: color-dodge;
      opacity: 0.8;
      background-size: contain;

      &_profile {
        background-image:
        url('../../../assets/images/apps/apps-profile.svg'),
        url('../../../assets/images/apps/apps-profile.svg'),
        url('../../../assets/images/apps/apps-profile.svg');
        background-repeat: no-repeat;
      }
      &_lenta {
        background-image:
        url('../../../assets/images/apps/apps-lenta.svg'),
        url('../../../assets/images/apps/apps-lenta.svg'),
        url('../../../assets/images/apps/apps-lenta.svg');
        background-repeat: no-repeat;
      }
      &_asap {
        background-image:
        url('../../../assets/images/apps/apps-asap.svg'),
        url('../../../assets/images/apps/apps-asap.svg'),
        url('../../../assets/images/apps/apps-asap.svg');
        background-repeat: no-repeat;
      }
      &_meetnet {
        background-image:
        url('../../../assets/images/apps/apps-meetnet.svg'),
        url('../../../assets/images/apps/apps-meetnet.svg'),
        url('../../../assets/images/apps/apps-meetnet.svg');
        background-repeat: no-repeat;
      }
      &_choose {
        background-image:
        url('../../../assets/images/apps/apps-profile.svg'),
        url('../../../assets/images/apps/apps-profile.svg'),
        url('../../../assets/images/apps/apps-profile.svg');
        background-repeat: no-repeat;
      }
      &_blackbox {
        background-image:
        url('../../../assets/images/apps/apps-blackbox.svg'),
        url('../../../assets/images/apps/apps-blackbox.svg'),
        url('../../../assets/images/apps/apps-blackbox.svg');
        background-repeat: no-repeat;
      }
      &_videos {
        background-image:
        url('../../../assets/images/apps/apps-videos.svg'),
        url('../../../assets/images/apps/apps-videos.svg'),
        url('../../../assets/images/apps/apps-videos.svg');
        background-repeat: no-repeat;
      }
      &_joblite {        
        background-image:
        url('../../../assets/images/apps/apps-joblite.svg'),
        url('../../../assets/images/apps/apps-joblite.svg'),
        url('../../../assets/images/apps/apps-joblite.svg');
        background-repeat: no-repeat;
      }
      &_findfriends {
        background-image:
        url('../../../assets/images/apps/apps-findfriends.svg'),
        url('../../../assets/images/apps/apps-findfriends.svg'),
        url('../../../assets/images/apps/apps-findfriends.svg');
        background-repeat: no-repeat;
      }

      &:hover {
        background-blend-mode: normal;
        opacity: 1;
      }
    }

    &-list-link {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      text-decoration: none;
      width: 100%;
      height: 100%;
      color: white;
    }

    &-list-link:hover {
      background-blend-mode: normal;
      color: colors.$blue;
    }

    &-list-title {
      white-space: nowrap;
      margin: 0;
      padding: 0;
    }
  }

  &__content {
    padding: 80px 0 20px;
    color: #222222;

    &-list {
      list-style: none;
      margin: 0;
      padding: 0;

      &-item {
        display: grid;
        grid-template-columns: 610px 1fr 10px;
        gap: 10px;
        padding: 65px 0;

        &-images-container {
          display: flex;
          justify-content: space-between;
        }

        &-image-wrapper {
          position: relative;

          &::after {
            display: block;
            content: 'iOS';
            font-size: 18px;
            line-height: 22px;
            font-weight: 400;
            width: auto;
            height: auto;
            text-align: center;
            margin: 23px 0 0;
          }
        }

        &-image-wrapper:last-of-type {
          &::after {
            content: 'Android';
          }
        }

        &-text-container {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }

        &-title {
          font-size: 40px;
          line-height: 48px;
          font-weight: 700;
          margin: 0 0 20px;
          padding: 0;
          width: 100%;
          max-width: 590px;
        }

        &-description  {
          font-size: 18px;
          line-height: 22px;
          font-weight: 400;
          margin: 0;
          padding: 0;
          width: 100%;
          max-width: 590px;

          & > p {
            margin: 0 0 12px;
            padding: 0;

            &:last-of-type {
              margin-bottom: 0;
            }
          }
        }

        &-bullets-container {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }

        &-bullet {
          display: block;
          width: 10px;
          height: 10px;
          background-color: #F2F1F1;
          border-radius: 50%;
          margin-bottom: 20px;
        }

        &-bullet_current {
          display: block;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          margin-bottom: 20px;
          background-color: #7185EC;
        }
      }
    }
  }

  &__dev {
    padding: 200px 0 50px;

    &-title {
      font-size: 48px;
      line-height: 58px;
      font-weight: 700;
      color: colors.$blue;
      padding: 0;
      margin: 0 0 36px;
    }
  }
}

.swiper {
  padding-right: 100px;

  &-slide {

    &__wrapper {
      padding: 36px 53px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    &__image-wrapper {
      max-width: 178px;
    }

    &__image {
      width: 100%;
      object-fit: contain;
    }

    &__title {
      font-size: 22px;
      line-height: 27px;
      font-weight: 600;
      white-space: nowrap;
      text-align: center;
    }
  }
}